import React, { Component } from 'react';
import './story_component.css';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import DogeCoin from '../photos/dogecoin.png';

class Blockchain extends Component {
  constructor(props) {
    super(props);
    this.state = {
      demo_room_state: 0,
      number_answer: "",
      demo_room_states: {
        0: {header_text: 'Okay, time to put insurance on the blockchain!', detail_text: 'It makes sense when you think about it', yes_button_text: 'Yeah, that sounds right', no_button_text: 'didn\'t bitcoin crash?'},
        1: {header_text: 'Time for DECENTRALIZED INSURANCE', detail_text: 'It\'s like the insurance version of democracy', yes_button_text: 'Let\'s do it!', no_button_text: 'what about privacy?'},
        2: {header_text: 'Woo hoo! We made a SuperInsureCoin to handle all of this', detail_text: 'Go to the sun room! That\'s where the CryptoStockExchange is!', yes_button_text: 'what?', no_button_text: false},
        3: {header_text: 'You\'re in the sun room, right?', detail_text: 'What number is taped to the table?', yes_button_text: 'Submit', no_button_text: 'I dunno'},
        
        4: {header_text: 'You\'re being unreasonable', detail_text: 'You made it this far! Try it again', yes_button_text: 'Fine', no_button_text: false},
        5: {header_text: 'You found the SUN ROOM!', detail_text: 'Time to sell that crypto stuff and make some money!', yes_button_text: 'Yay!', no_button_text: false},
        6: {header_text: 'Wrong!', detail_text: 'You gotta go to the CERTAINLY AUTO room', yes_button_text: 'Okay, okay', no_button_text: 'This game is stupid'}
      }
    };
  }

  render() {
    const set_next_state = (value, affirmative_state, negative_state=4) => {
      if (value) {
        this.setState({demo_room_state: affirmative_state});
      } else {
        this.setState({demo_room_state: negative_state});
      }
    }

    const handle_number_change = event => {
      this.setState({number_answer: event.target.value});
    }

    const handle_button_click = (value) => {
      switch(this.state.demo_room_state) {
        case 0:
          // go to gigUp room
          set_next_state(value, 1);
          return;
        case 1:
          // go to THS room
          set_next_state(value, 2);
          return;
        case 2:
          // lots of casualties
          set_next_state(value, 3);
          return;
        case 3:
          // prove it!
          if (this.state.number_answer === "8") {
            // go to "right!"
            set_next_state(value, 5, 5);
            return;
          }
          // go to "wong"
          set_next_state(value, 6);
          return;
        case 4:
          // don't be a stinker
          set_next_state(value, 0);
          return;
        case 5:
          // here's the disruptive technology
          this.props.onMoveForward('CRYPTO_FAIL');
          return;
        case 6:
          // wrong on the number!
          set_next_state(value, 3);
          return;
        default:
          alert('how even did you get here?');
          console.log(this.state.demo_room_state);
          return;
      }
    }

    return (
        <div className="Story-Component">
          <div className="Story-Text">
            <Typography variant="h5" component="h3">
              {this.state.demo_room_states[this.state.demo_room_state].header_text}
            </Typography>
            <Typography component="p">
              {this.state.demo_room_states[this.state.demo_room_state].detail_text}
            </Typography>
          </div>
          {
            (this.state.demo_room_state === 3) &&
            <div>
              <TextField
                id="standard-number"
                value={this.state.number_answer}
                onChange={handle_number_change}
                type="number"
              />
              <br/>
              <br/>
            </div>
          }
          {
            (this.state.demo_room_state === 5) &&
            <div>
              <img src={DogeCoin} className="Eugene" alt="doge_coin" />
              <br/>
              <br/>
            </div>
          }
          {
            this.state.demo_room_states[this.state.demo_room_state].yes_button_text &&
              <Button className="StoryButton" variant="outlined" onClick={() => handle_button_click(true)}>
                {this.state.demo_room_states[this.state.demo_room_state].yes_button_text}
              </Button>
          }
          {
            this.state.demo_room_states[this.state.demo_room_state].no_button_text &&
            <Button className="StoryButton" variant="outlined" onClick={() => handle_button_click(false)}>
              {this.state.demo_room_states[this.state.demo_room_state].no_button_text}
            </Button>
          }
          <br/>
          <br/>
          <br/>
          <i className="fab fa-bitcoin fa-7x"></i>
        </div>
    );
  }
}

export default Blockchain;
