import React, { Component } from 'react';
import './story_component.css';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import TeleportPic from '../photos/teleporters.png';

class Teleporter extends Component {
  constructor(props) {
    super(props);
    this.state = {
      demo_room_state: 0,
      number_answer: "",
      demo_room_states: {
        0: {header_text: 'Let\'s insure some teleporters', detail_text: 'Go to the gigUp room', yes_button_text: 'But gigUp is dead!', no_button_text: false},
        1: {header_text: 'Oh, you\'re right...', detail_text: 'Try the THS room?', yes_button_text: 'Is that even a thing?', no_button_text: false},
        2: {header_text: 'Wow, innovation has a lot of casualties, doesn\'t it?', detail_text: 'Try the certainly auto room. They probably know how to insure teleporters', yes_button_text: 'Okay, here I go', no_button_text: false},
        3: {header_text: 'Next question:', detail_text: 'What number is taped to the door?', yes_button_text: 'Submit', no_button_text: 'I dunno'},
        
        4: {header_text: 'You\'re being unreasonable', detail_text: 'You made it this far! Try it again', yes_button_text: 'Fine', no_button_text: false},
        5: {header_text: 'You found the CERTAINLY AUTO room!', detail_text: 'You can ask them how to insure teleporters!', yes_button_text: 'Yay!', no_button_text: false},
        6: {header_text: 'Wrong!', detail_text: 'You gotta go to the CERTAINLY AUTO room', yes_button_text: 'Okay, okay', no_button_text: 'This game is stupid'}
      }
    };
  }

  render() {
    const set_next_state = (value, affirmative_state, negative_state=4) => {
      if (value) {
        this.setState({demo_room_state: affirmative_state});
      } else {
        this.setState({demo_room_state: negative_state});
      }
    }

    const handle_number_change = event => {
      this.setState({number_answer: event.target.value});
    }

    const handle_button_click = (value) => {
      switch(this.state.demo_room_state) {
        case 0:
          // go to gigUp room
          set_next_state(value, 1, 1);
          return;
        case 1:
          // go to THS room
          set_next_state(value, 2);
          return;
        case 2:
          // lots of casualties
          set_next_state(value, 3);
          return;
        case 3:
          // prove it!
          if (this.state.number_answer === "7") {
            // go to "right!"
            set_next_state(value, 5, 5);
            return;
          }
          // go to "wong"
          set_next_state(value, 6);
          return;
        case 4:
          // don't be a stinker
          set_next_state(value, 0);
          return;
        case 5:
          // here's the disruptive technology
          this.props.onMoveForward('TELEPORT_FAIL');
          return;
        case 6:
          // wrong on the number!
          set_next_state(value, 3);
          return;
        default:
          alert('how even did you get here?');
          console.log(this.state.demo_room_state);
          return;
      }
    }

    return (
        <div className="Story-Component">
          <div className="Story-Text">
            <Typography variant="h5" component="h3">
              {this.state.demo_room_states[this.state.demo_room_state].header_text}
            </Typography>
            <Typography component="p">
              {this.state.demo_room_states[this.state.demo_room_state].detail_text}
            </Typography>
          </div>
          {
            (this.state.demo_room_state === 3) &&
            <div>
              <TextField
                id="standard-number"
                value={this.state.number_answer}
                onChange={handle_number_change}
                type="number"
              />
              <br/>
              <br/>
            </div>
          }
          {
            (this.state.demo_room_state === 5) &&
            <div>
              <img src={TeleportPic} className="Eugene" alt="teleporter" />
              <br/>
              <br/>
            </div>
          }
          {
            this.state.demo_room_states[this.state.demo_room_state].yes_button_text &&
              <Button className="StoryButton" variant="outlined" onClick={() => handle_button_click(true)}>
                {this.state.demo_room_states[this.state.demo_room_state].yes_button_text}
              </Button>
          }
          {
            this.state.demo_room_states[this.state.demo_room_state].no_button_text &&
            <Button className="StoryButton" variant="outlined" onClick={() => handle_button_click(false)}>
              {this.state.demo_room_states[this.state.demo_room_state].no_button_text}
            </Button>
          }
          <br/>
          <br/>
          <br/>
          <i className="far fa-arrow-alt-circle-up fa-7x"></i>
        </div>
    );
  }
}

export default Teleporter;
