import React, { Component } from 'react';
import './story_component.css';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';

class TeleportFail extends Component {
  constructor(props) {
    super(props);
    this.state = {
      demo_room_state: 0,
      number_answer: "",
      demo_room_states: {
        0: {header_text: 'Awkward', detail_text: 'Teleporters don\'t exist yet. Duh', yes_button_text: 'Then why did you ask me to insure them?', no_button_text: 'Yes they do'},
        1: {header_text: 'No need to place blame', detail_text: 'Okay fellow Solarian, time to PIVOT! What should we try to insure?', yes_button_text: 'Sports?', no_button_text: 'Lunches!'},
        4: {header_text: 'Don\'t be silly', detail_text: 'Teleporters don\'t exist', yes_button_text: 'Fine', no_button_text: false},
      }
    };
  }

  render() {
    const set_next_state = (value, affirmative_state, negative_state=4) => {
      if (value) {
        this.setState({demo_room_state: affirmative_state});
      } else {
        this.setState({demo_room_state: negative_state});
      }
    }

    const handle_number_change = event => {
      this.setState({number_answer: event.target.value});
    }

    const handle_button_click = (value) => {
      switch(this.state.demo_room_state) {
        case 0:
          // teleporters don't exist
          set_next_state(value, 1);
          return;
        case 1:
          // change of plan. What to do?
          const next_place = value ? 'PING_PONG' : 'LUNCHES';
          this.props.onMoveForward(next_place);
          return;
        case 4:
          // seriously, they don't exist
          set_next_state(value, 0);
          return;
        default:
          alert('how even did you get here?');
          console.log(this.state.demo_room_state);
          return;
      }
    }

    return (
        <div className="Story-Component">
          <div className="Story-Text">
            <Typography variant="h5" component="h3">
              {this.state.demo_room_states[this.state.demo_room_state].header_text}
            </Typography>
            <Typography component="p">
              {this.state.demo_room_states[this.state.demo_room_state].detail_text}
            </Typography>
          </div>
          {
            (this.state.demo_room_state === 3) &&
            <div>
              <TextField
                id="standard-number"
                value={this.state.number_answer}
                onChange={handle_number_change}
                type="number"
              />
              <br/>
              <br/>
            </div>
          }
          {
            this.state.demo_room_states[this.state.demo_room_state].yes_button_text &&
              <Button className="StoryButton" variant="outlined" onClick={() => handle_button_click(true)}>
                {this.state.demo_room_states[this.state.demo_room_state].yes_button_text}
              </Button>
          }
          {
            this.state.demo_room_states[this.state.demo_room_state].no_button_text &&
            <Button className="StoryButton" variant="outlined" onClick={() => handle_button_click(false)}>
              {this.state.demo_room_states[this.state.demo_room_state].no_button_text}
            </Button>
          }
          <br/>
          <br/>
          <br/>
          <i class="far fa-arrow-alt-circle-up fa-7x"></i>
        </div>
    );
  }
}

export default TeleportFail;
