import React, { Component } from 'react';
import './story_component.css';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import DemoRoomPic from '../photos/demo_room.jpg';

class DemoRoom extends Component {
  constructor(props) {
    super(props);
    this.state = {
      demo_room_state: 0,
      number_answer: "",
      demo_room_states: {
        0: {header_text: 'Okay! Let\'s get started', detail_text: 'Are you in the demo room?', yes_button_text: 'Yes', no_button_text: 'No'},
        1: {header_text: 'Alrighty. Go to the demo room!', detail_text: '', yes_button_text: 'Okay', no_button_text: 'No'},
        2: {header_text: 'Are you sure you\'re in the demo room?', detail_text: '', yes_button_text: 'Yes', no_button_text: 'No'},
        3: {header_text: 'Prove it!', detail_text: 'what is the green number underneath the fire alarm on the back wall?', yes_button_text: 'I told you', no_button_text: 'I dunno'},
        4: {header_text: 'Oh, come on!', detail_text: 'Don\'t be a stinker! Let\'s try this again', yes_button_text: 'Fine', no_button_text: false},
        5: {header_text: 'Okay sweet! Here are a couple DISRUPTIVE TECHNOLOGIES', detail_text: 'Pick One!', yes_button_text: 'Teleporter Insurance', no_button_text: 'Insurance on the BLOCKCHAIN'},
        6: {header_text: 'Wrong!', detail_text: 'the correct answer is 5', yes_button_text: 'Okay, okay', no_button_text: 'This game is stupid'}
      }
    };
  }

  render() {
    const set_next_state = (value, affirmative_state, negative_state) => {
      if (value) {
        this.setState({demo_room_state: affirmative_state});
      } else {
        this.setState({demo_room_state: negative_state});
      }
    }

    const handle_number_change = event => {
      this.setState({number_answer: event.target.value});
    }

    const handle_button_click = (value) => {
      switch(this.state.demo_room_state) {
        case 0:
          // let's get started
          set_next_state(value, 2, 1);
          return;
        case 1:
          // go to the demo room
          set_next_state(value, 0, 4);
          return;
        case 2:
          // are you sure you're in the demo room?
          set_next_state(value, 3, 4);
          return;
        case 3:
          // prove it!
          console.log(this.state);
          if (this.state.number_answer === "5") {
            set_next_state(value, 5, 5);
            return;
          }
          set_next_state(value, 6, 4);
          return;
        case 4:
          // don't be a stinker
          set_next_state(value, 0, 4);
          return;
        case 5:
          // here's the disruptive technology
          if (value) {
            this.props.onMoveForward('TELEPORTER');
            return;
          }
          this.props.onMoveForward('BLOCKCHAIN');
          return;
        case 6:
          // wrong on the number!
          set_next_state(value, 3, 4);
          return;
        default:
          alert('how even did you get here?');
          console.log(this.state.demo_room_state);
          return;
      }
    }

    return (
        <div className="Story-Component">
          <div className="Story-Text">
            <Typography variant="h5" component="h3">
              {this.state.demo_room_states[this.state.demo_room_state].header_text}
            </Typography>
            <Typography component="p">
              {this.state.demo_room_states[this.state.demo_room_state].detail_text}
            </Typography>
          </div>
          {
            (this.state.demo_room_state === 3) &&
            <div>
              <TextField
                id="standard-number"
                value={this.state.number_answer}
                onChange={handle_number_change}
                type="number"
              />
              <br/>
              <br/>
            </div>
          }
          {
            (this.state.demo_room_state === 5) &&
            <div>
              <img src={DemoRoomPic} className="Eugene" alt="demo_room" />
              <br/>
              <br/>
            </div>
          }
          {
            this.state.demo_room_states[this.state.demo_room_state].yes_button_text &&
              <Button className="StoryButton" variant="outlined" onClick={() => handle_button_click(true)}>
                {this.state.demo_room_states[this.state.demo_room_state].yes_button_text}
              </Button>
          }
          {
            this.state.demo_room_states[this.state.demo_room_state].no_button_text &&
            <Button className="StoryButton" variant="outlined" onClick={() => handle_button_click(false)}>
              {this.state.demo_room_states[this.state.demo_room_state].no_button_text}
            </Button>
          }
          <br/>
          <br/>
          <br/>
          <i className="fas fa-tv fa-7x"></i>
        </div>
    );
  }
}

export default DemoRoom;
