import React, { Component } from 'react';
import './story_component.css';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Eugene from '../photos/eugene.jpg';
import Emily from '../photos/emily.JPG';

class Winner extends Component {
  constructor(props) {
    super(props);
    this.state = {
      demo_room_state: 0,
      number_answer: "",
      demo_room_states: {
        0: {header_text: 'Good job, fellow Solarian', detail_text: 'You innovated!', yes_button_text: 'Cool!', no_button_text: false},
        1: {header_text: 'As for your prize...', detail_text: '(Starbucks giftcard or king sized candy bar)', yes_button_text: 'WHAT?', no_button_text: false},
        4: {header_text: 'Find Emily or Eugene!', detail_text: '', yes_button_text: false, no_button_text: false},
      }
    };
  }

  render() {
    const set_next_state = (value, affirmative_state, negative_state=4) => {
      if (value) {
        this.setState({demo_room_state: affirmative_state});
      } else {
        this.setState({demo_room_state: negative_state});
      }
    }

    const handle_number_change = event => {
      this.setState({number_answer: event.target.value});
    }

    const handle_button_click = (value) => {
      switch(this.state.demo_room_state) {
        case 0:
          // teleporters don't exist
          set_next_state(value, 1);
          return;
        case 1:
          // change of plan. What to do?
          set_next_state(value, 4);
          return;
        case 4:
          // seriously, they don't exist
          set_next_state(value, 0);
          return;
        default:
          alert('how even did you get here?');
          console.log(this.state.demo_room_state);
          return;
      }
    }

    return (
        <div className="Story-Component">
          <div className="Story-Text">
            <Typography variant="h5" component="h3">
              {this.state.demo_room_states[this.state.demo_room_state].header_text}
            </Typography>
            <Typography component="p">
              {this.state.demo_room_states[this.state.demo_room_state].detail_text}
            </Typography>
          </div>
          {
            (this.state.demo_room_state === 4) &&
            <div>
              <img src={Emily} className="Eugene" alt="emily" /><br/><br/>
              <img src={Eugene} className="Eugene" alt="eugene" /><br/><br/>
            </div>
          }
          {
            this.state.demo_room_states[this.state.demo_room_state].yes_button_text &&
              <Button className="StoryButton" variant="outlined" onClick={() => handle_button_click(true)}>
                {this.state.demo_room_states[this.state.demo_room_state].yes_button_text}
              </Button>
          }
          {
            this.state.demo_room_states[this.state.demo_room_state].no_button_text &&
            <Button className="StoryButton" variant="outlined" onClick={() => handle_button_click(false)}>
              {this.state.demo_room_states[this.state.demo_room_state].no_button_text}
            </Button>
          }
          <br/>
          <br/>
          <br/>
          <i className="fa fa-award fa-7x"></i>
        </div>
    );
  }
}

export default Winner;
