import React, { Component } from 'react';
import './story_component.css';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';

class PingPong extends Component {
  constructor(props) {
    super(props);
    this.state = {
      demo_room_state: 0,
      number_answer: "",
      demo_room_states: {
        0: {header_text: 'Okay, let\'s insure some sports', detail_text: 'Is it even legal to insure sports? Is it like enabling bets?', yes_button_text: 'Don\'t think about it, let\'s go', no_button_text: 'Maybe we should check with legal'},
        1: {header_text: 'I like your attitude.', detail_text: 'Let\'s go to the ping pong tables', yes_button_text: 'okay, here I go', no_button_text: false},
        2: {header_text: 'Okay, so maybe we shouldn\'t get into gambling.', detail_text: 'Why don\'t we insure limbs, or something?', yes_button_text: 'Like in soccer?', no_button_text: 'that sounds like health insurance...'},
        3: {header_text: 'You know the drill. Go to the ping pong room!', detail_text: 'What word is taped underneath the ping pong table?', yes_button_text: 'Submit', no_button_text: false},
        
        4: {header_text: 'Wrong answer', detail_text: 'don\'t be a spoilsport. We gotta INNOVATE!', yes_button_text: 'Jeez, okay!', no_button_text: 'Are you insane?'},
        5: {header_text: 'You found the PING PONG TABLES!', detail_text: 'That\' how you know we\'re just like a startup', yes_button_text: 'Yay!', no_button_text: 'how does this help me with sports insurance'},
        6: {header_text: 'Don\'t think about it too hard', detail_text: 'Just be happy about the pong', yes_button_text: 'Jeez, okay!', no_button_text: 'Seriously, are you insane?'}
      }
    };
  }

  render() {
    const set_next_state = (value, affirmative_state, negative_state=4) => {
      if (value) {
        this.setState({demo_room_state: affirmative_state});
      } else {
        this.setState({demo_room_state: negative_state});
      }
    }

    const handle_number_change = event => {
      this.setState({number_answer: event.target.value});
    }

    const handle_button_click = (value) => {
      switch(this.state.demo_room_state) {
        case 0:
          // Are sports legal?
          set_next_state(value, 1, 2);
          return;
        case 1:
          // sports are legal. Let's go to the ping pong room
          set_next_state(value, 3);
          return;
        case 2:
          // let's not gample
          set_next_state(value, 1, 4);
          return;
        case 3:
          // prove it!
          if (this.state.number_answer === "101") {
            // go to "right!"
            set_next_state(value, 5);
            return;
          }
          // go to "wong"
          set_next_state(value, 4);
          return;
        case 4:
          // don't be a spoilsport
          if (value) {
            set_next_state(value, 2);
          } else {
            this.props.onMoveForward('ATLASSIAN_HELL');
          }
          return;
        case 5:
          // here's the disruptive technology
          if (value) {
            this.props.onMoveForward('WINNER');
          } else {
            set_next_state(value, 3, 6);
          }
          return;
        case 6:
          // wrong on the number!
          if (value) {
            this.props.onMoveForward('WINNER');
          } else {
            this.props.onMoveForward('ATLASSIAN_HELL');
          }
          return;
        default:
          alert('how even did you get here?');
          console.log(this.state.demo_room_state);
          return;
      }
    }

    return (
        <div className="Story-Component">
          <div className="Story-Text">
            <Typography variant="h5" component="h3">
              {this.state.demo_room_states[this.state.demo_room_state].header_text}
            </Typography>
            <Typography component="p">
              {this.state.demo_room_states[this.state.demo_room_state].detail_text}
            </Typography>
          </div>
          {
            (this.state.demo_room_state === 3) &&
            <div>
              <TextField
                id="standard-number"
                value={this.state.number_answer}
                onChange={handle_number_change}
                type="number"
              />
              <br/>
              <br/>
            </div>
          }
          {
            (this.state.demo_room_state === 5) &&
            <div>
              <iframe src="https://giphy.com/embed/4IAzyrhy9rkis"
              width="300" frameBorder="0" class="giphy-embed" allowFullScreen></iframe>
              <br/>
              <br/>
            </div>
          }

          {
            this.state.demo_room_states[this.state.demo_room_state].yes_button_text &&
              <Button className="StoryButton" variant="outlined" onClick={() => handle_button_click(true)}>
                {this.state.demo_room_states[this.state.demo_room_state].yes_button_text}
              </Button>
          }
          {
            this.state.demo_room_states[this.state.demo_room_state].no_button_text &&
            <Button className="StoryButton" variant="outlined" onClick={() => handle_button_click(false)}>
              {this.state.demo_room_states[this.state.demo_room_state].no_button_text}
            </Button>
          }
          <br/>
          <br/>
          <br/>
          <i className="fas fa-table-tennis fa-7x"></i>
        </div>
    );
  }
}

export default PingPong;
