import React, { Component } from 'react';
import './story_component.css';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';

class Teleporter extends Component {
  constructor(props) {
    super(props);
    this.state = {
      demo_room_state: 0,
      number_answer: "",
      demo_room_states: {
        0: {header_text: 'Okay, lunches. How do we insure lunches...?', detail_text: '', yes_button_text: 'Maybe by offering a reasonably priced alternative in a convenient place?', no_button_text: 'why would you ever insure a lunch?'},
        1: {header_text: 'That sounds familiar, doesn\'t it?', detail_text: 'Where can you buy food at lunchtime?', yes_button_text: 'On the fifth floor?', no_button_text: 'Nowhere. It\'s hopeless'},
        2: {header_text: 'Okay, try going to the fifth floor fooda area!', detail_text: 'Go to wherever they sell lunches', yes_button_text: 'Okay, here I go', no_button_text: false},
        3: {header_text: 'You know what\'s coming up:', detail_text: 'Can you find the lunch number??', yes_button_text: 'Submit', no_button_text: 'No!!'},
        
        4: {header_text: 'Oh, come on! Play along, Solarian!', detail_text: 'It\'s like herding cats with you', yes_button_text: 'Okay, calm down, I\'ll play along', no_button_text: 'but this is stupid!'},
        5: {header_text: 'You got some LUNCH INSURANCE!', detail_text: 'You\'ll never be hungry again!', yes_button_text: 'FOOD', no_button_text: false},
        6: {header_text: 'You got the number wrong!', detail_text: 'are you sure you\'re on the fifth floor?', yes_button_text: 'Oops, I\'ll try again', no_button_text: 'This game is stupid'}
      }
    };
  }

  render() {
    const set_next_state = (value, affirmative_state, negative_state=4) => {
      if (value) {
        this.setState({demo_room_state: affirmative_state});
      } else {
        this.setState({demo_room_state: negative_state});
      }
    }

    const handle_number_change = event => {
      this.setState({number_answer: event.target.value});
    }

    const handle_button_click = (value) => {
      switch(this.state.demo_room_state) {
        case 0:
          // lunch insurance?
          set_next_state(value, 1);
          return;
        case 1:
          // sounds like fooda
          set_next_state(value, 2);
          return;
        case 2:
          // go to the fifth floor
          set_next_state(value, 3);
          return;
        case 3:
          // find the number
          if (this.state.number_answer === "22") {
            // go to "right!"
            set_next_state(value, 5);
            return;
          }
          // go to "wong"
          set_next_state(value, 6);
          return;
        case 4:
          // don't be a stinker
          if (!value) this.props.onMoveForward('ATLASSIAN_HELL');;
          set_next_state(value, 0);
          return;
        case 5:
          // here's the disruptive technology
          this.props.onMoveForward('WINNER');
          return;
        case 6:
          // wrong on the number!
          if (!value) this.props.onMoveForward('ATLASSIAN_HELL');;
          set_next_state(value, 3);
          return;
        default:
          alert('how even did you get here?');
          console.log(this.state.demo_room_state);
          return;
      }
    }

    return (
        <div className="Story-Component">
          <div className="Story-Text">
            <Typography variant="h5" component="h3">
              {this.state.demo_room_states[this.state.demo_room_state].header_text}
            </Typography>
            <Typography component="p">
              {this.state.demo_room_states[this.state.demo_room_state].detail_text}
            </Typography>
          </div>
          {
            (this.state.demo_room_state === 3) &&
            <div>
              <TextField
                id="standard-number"
                value={this.state.number_answer}
                onChange={handle_number_change}
                type="number"
              />
              <br/>
              <br/>
            </div>
          }
          {
            (this.state.demo_room_state === 5) &&
            <div>
              <iframe src="https://giphy.com/embed/GnCc88zZhSVUc" width="300" frameBorder="0" class="giphy-embed" allowFullScreen></iframe>
              <br/>
              <br/>
            </div>
          }
          {
            this.state.demo_room_states[this.state.demo_room_state].yes_button_text &&
              <Button className="StoryButton" variant="outlined" onClick={() => handle_button_click(true)}>
                {this.state.demo_room_states[this.state.demo_room_state].yes_button_text}
              </Button>
          }
          {
            this.state.demo_room_states[this.state.demo_room_state].no_button_text &&
            <Button className="StoryButton" variant="outlined" onClick={() => handle_button_click(false)}>
              {this.state.demo_room_states[this.state.demo_room_state].no_button_text}
            </Button>
          }
          <br/>
          <br/>
          <br/>
          <i className="far fa-hamburger fa-7x"></i>
        </div>
    );
  }
}

export default Teleporter;
