import React, { Component } from 'react';
import './story_component.css';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';

class QuickStart extends Component {
  constructor(props) {
    super(props);
    this.state = {
      header_text: 'Welcome, fellow Solarian!',
      detail_text: 'We must find the ultimate DISRUPTIVE TECHNOLOGY to fulfill our innovative destiny'
    };
  }

  state = {
  };
  render() {
    const handle_button_click = (value) => {
      if (value) {
        this.props.onMoveForward('DEMO_ROOM');
      }
      this.setState({
        header_text: 'Are you kidding me?',
        detail_text: 'What kind of solarian DOESN\'T want to go an an adventure? Let\'s try that again.'
      });
    }

    return (
        <div className="Story-Component">
          <div className="Story-Text">
            <Typography variant="h5" component="h3">
              {this.state.header_text}
            </Typography>
            <Typography component="p">
              {this.state.detail_text}
            </Typography>
            <Typography component="p">
              Are you ready?
            </Typography>
          </div>
          <Button className="StoryButton" variant="outlined" onClick={() => handle_button_click(true)}>
            Yes
          </Button>
          <Button className="StoryButton" variant="outlined" onClick={() => handle_button_click(false)}>
            No
          </Button>
        </div>
    );
  }
}

export default QuickStart;
